import Vue from 'vue'
import Router from 'vue-router'
import MuseUI from 'muse-ui'

import 'muse-ui/dist/muse-ui.css'

import VueLazyload from "vue-lazyload"

require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

//import Hello from '@/components/Hello'

//import Home from '@/components/Home'
//import Book from '@/components/Book'
//import VideoDetails from '@/components/VideoDetails'
//import VipVideoView from '@/components/VipVideoView'
//import GirlHome from '@/components/GirlHome'
//import Live from '@/components/Live'
import Login from '@/components/Login'
import VueResource from 'vue-resource'
import AwesomSwiper from 'vue-awesome-swiper'


import VueVideoPlayer from 'vue-video-player'



const Book=r => require.ensure([], () => r(require('.././components/Book.vue')))

const Index=r => require.ensure([], () => r(require('.././components/Index.vue')))

const Home=r => require.ensure([], () => r(require('.././components/Home.vue')))

const VideoDetails=r => require.ensure([], () => r(require('.././components/VideoDetails.vue')))

const VipVideoView=r => require.ensure([], () => r(require('.././components/VipVideoView.vue')))

const VideoView=r => require.ensure([], () => r(require('.././components/VideoView.vue')))

const GirlHome=r => require.ensure([], () => r(require('.././components/GirlHome.vue')))

const Live=r => require.ensure([], () => r(require('.././components/LiveView.vue')))

const AgeAuth=r => require.ensure([], () => r(require('.././components/AgeAuth.vue')))

const Profile=r => require.ensure([], () => r(require('.././components/Profile.vue')))

const Help=r => require.ensure([], () => r(require('.././components/Help.vue')))

const About=r => require.ensure([], () => r(require('.././components/About.vue')))

const Price=r => require.ensure([], () => r(require('.././components/Price.vue')))

//const Login=r => require.ensure([], () => r(require('.././components/Login.vue')))

Vue.use(AwesomSwiper)
Vue.use(VueResource)
Vue.use(Router)
Vue.use(MuseUI)
Vue.use(VueLazyload, {
  loading:'http://pic12.nipic.com/20110209/2897814_144722712129_2.gif'
})
Vue.use(VueVideoPlayer)



export default new Router({
  routes: [
   {
      path: '/',
     redirect:'/ageAuth'
    },
    {
       path:'/ageAuth',
       name: 'AgeAuth',
       component:AgeAuth
    },
    {  path:'/index',
       name: 'Index',
      component:Index
    },
    {
      path:'/login',
      name:'Login',
      component:Login
    },
    {
      path:  "/video",
      name:  'Video',
      component: VideoView
    },
    {
      path:"/picture",
      name:'Picture',
      component:GirlHome
    },
    {
      path:'/profile',
      name:'Profile',
      component:Profile
    },
    {
      path:"/book",
      name:'Book',
      component:Book
    },
    {
      path:"/vipvideo",
      name:"VipVideo",
      component:VipVideoView
    },
    {
      path:'/livevideo',
      name:'livevideo',
      component:Live
    },
    {
      path:'/help',
      name:'help',
      component:Help,
    },
    {
      path:'/about',
      name:'about',
      component:About,
    },
    {
      path:'/price',
      name:'price',
      component:Price,
    }
  ]
})



