<template>
  <div id="loginview" style="position:fixed;margin:0px;height:100%;width:100%;
  background-color: #c86dd7;
         background: -moz-linear-gradient(-45deg,  #3023ae 0%, #c86dd7 100%);
        background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,#3023ae), color-stop(100%,#c86dd7));
        background: -webkit-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%) ;
        background: -o-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
        background: -ms-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
        background: linear-gradient(135deg,  #3023ae 0%,#c86dd7 100%);
        background-attachment: fixed;
        overflow-y:scroll;  
  
  ">
   <mu-appbar  title="登錄">
   <mu-icon-button icon="navigate_before" @click="goback()" slot="left" />
 </mu-appbar>
 
 <div class="center">

  <img src="https://ooo.0o0.ooo/2017/06/24/594e330a587a5.jpg" style="width:48px;">
  <mu-text-field  label="帳號" hintText="建议邮箱,或者qq,以便忘記密碼時方便找回" fullWidth   v-model="username"  labelFloat/><br/>
  <mu-text-field  label="密碼" hintText="請輸入您的密碼，至少六位" fullWidth  type="password" v-model="password" labelFloat/><br/>
  <mu-text-field  v-if="noAccount"  label="邀請碼" hintText="請輸入邀請碼，如無保持為空" fullWidth   labelFloat  v-model="icode"/>
  
  <br><br>
  <mu-raised-button  v-if="noAccount" label="註冊" @click="register()"  fullWidth  primary/>
  <mu-raised-button v-if="!noAccount" label="登錄" @click="login(1)"  fullWidth primary/>
  </br>
  </br>
  <div style="display:flex;justify-content:center;align-items:center">
   <mu-flat-button  :label="noAccount?'  立即登錄':'註冊'" v-model="noAccount" @click="noAccount=!noAccount" />
    <h5>|</h5>
   <mu-flat-button  :label="noAccount?'獲取邀請碼':'忘記密碼?'" v-model="noAccount" @click="tip()" />
   </div>
  <br></br>

  <h5>註冊登錄即代表您同意本服務及隱私條款</h5>
  
</div>

</div>
</template>
<script >
  export default {
    name:'login',
    data(){
      return {
       noAccount:true,
       username:"",
       password:"",
       icode:""
     }
   },
   
   methods: {

    login(usertype)
    {   
      if(usertype==0){

        this.username=null
        this.password=null
        this.icode=null
        
        if(!localStorage.getItem("canView")){

           alert("已經體驗10分鐘了")
           this.noAccount=true
           return
        }
        
      }

      if(this.username.length>3 &&this.password.length>5) {
        
           this.$http.post(this.$store.state.serverUrl+"/login",{username:this.username,password:this.password},{
                            headers: {'Content-Type':'application/json'}
                        }).then(
                   response=>{ 
                   
                    if(response.body['token']){
                      localStorage.user=JSON.stringify(response.body)
                      this.$store.state.loginedUser=response.body
                     
                      localStorage.setItem("canView",false)
                      this.$router.push({path:'/profile'}) 
                    } 
                  else {
                     alert("登陸失敗，請確認妳的用戶名密碼正確！")
                  }
                },response=>{
                  alert(response.body)
              
          })  
      }
      else {
        alert("用戶名和密码不能小于5位")
      }
      
          //this.$router.push({path:'/index'})
          
        },
        register()
        {  
           if(this.username.length>5&&this.password.length>5){
               this.$http.post(this.$store.state.serverUrl+"/register",{username:this.username,password:this.password,icode:this.icode},{
                            headers: {'Content-Type':'application/json; charset=UTF-8'}
                        }).then(
                response=>{ 
                
                  if(response.body['token']){
                    
                    alert("註冊成功！您的賬戶為:"+this.username+"密碼為："+this.password+",請牢記！，您当前免费获赠一天的VIP资格时间,为了不影响您的观看，请及时充值！")
                    localStorage.user=JSON.stringify(response.body)
                    this.$store.state.loginedUser=response.body
                     
                    localStorage.setItem("canView",false)
                    this.$router.push({path:'/profile'}) 
                  } 
                  else {
                    alert(response.body['msg'])
                  }
                },response=>{
                  alert(response.body['msg'])
                })
           }
           else {
               alert("用户名和密码要求至少六位！")
           } 
        },
        goback()
        {
          this.$router.push({path:"/index"})
        },
        tip(){
           alert("请联系客服！")
        }
        
      } 
    }
  </script>
  <style scoped>
    
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
  }
  .center{
   margin: 50px;
   margin-top: 80px;
   
   text-align: center;
   
 }
 .desc-container{
  height:100%;
  overflow-y: scroll;
}
img {
  width: 100%;
}
.fixed {
  position:fixed;
  top: 0;

}

</style>


